
import swal from "sweetalert";



export const alertAdress = () => {
    return swal("Adresse copier !", "Vous pouvez maintenant coller la slection", "success");
};


export const alertEmail = () => {
    return swal("E-mail copier !", "Vous pouvez maintenant coller la slection", "success");
};


export const alertPhoneNumber = () => {
    return swal("Tlphone copier !", "Vous pouvez maintenant coller la slection", "success");
};

